import React, { useContext, useEffect } from 'react';
import './../../Css/landing-page-style.css';
import { CustomerContext } from './CustomerContext';
import axios from 'axios';
import { ContextReset } from '../ContextReset';
import { useForm } from 'react-hook-form';

const CustomerRegistration = () => {
   const [ custRegister, setcustRegister ] = useContext(CustomerContext);
   const [ myreset, setReset ] = useContext(ContextReset);
   const { register, handleSubmit, errors } = useForm();

//    useEffect(()=>{
//       setcustRegister([{firstName:'', lastName:'', email:'', phoneNumber:''}]);
//    },[]);

    const onSubmit = (data) =>{
         console.log(data);
         setcustRegister(data);
         setTimeout(()=>console.log(custRegister),2000);
         //console.log(custRegister);
    }

    const Form_clear = () => {
        setcustRegister([{firstName:'', lastName:'', email:'', phoneNumber:''}]);
        console.log(custRegister);
        const x = document.getElementsByClassName("input-form");
        for(var i = 0; i < x.length; i++){
            x[i].value = "";
        } 
    }

    
        return(
            <div id="reg-form" className="register-form">
                <h1>Please Fill The Form</h1>
                <form onSubmit={ handleSubmit(onSubmit) }>
                   <input id="firstName" className="input-form" type="text" name="firstName"  ref={register} placeholder="Your name"/><br/>
                   <input className="input-form" type="text" name="lastName"   ref={register} placeholder="Enter your last name" /><br/>
                   <input className="input-form" type="text" name="email"      ref={register} placeholder="Enter your email" /><br/>
                   <input className="input-form" type="text" name="phoneNumber" ref={register} placeholder="Enter your phone number" /><br/>
                   <button className="form-btn"  type="submit">Register</button> <br/>     
                </form>

                <button className="form-btn" onClick={ Form_clear }>Clear</button> <br/>
            </div>
        )
}

export default CustomerRegistration;